import React, { useState } from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import { userInfo } from '../../constants/user'
import "./About-style.scss"

export default function About() {
    const [width, setWidth] = useState(window.innerWidth)

    window.addEventListener("resize", () => {
        if (width !== window.innerWidth) {
            setWidth(window.innerWidth)
        }
    })

    const downloadCv = () => {
        window.location.href = userInfo.cvLink
    }

    return (
        <Container id="About" fluid={true} className="container about-container">
            <Row>
                {
                    width > 920 && (
                        <Col className="col1-about">
                            <img
                                className="about-img"
                                src={require("../../assets/Images/about-img.webp")} alt="avatar" />
                        </Col>
                    )
                }
                <Col className="col2-about">
                    <div>
                        <h1 className="page-title">About Me</h1>
                        <h5 className="title-about">{userInfo.subDisc}</h5>
                        <p className="description">{userInfo.desc}</p>
                        <br />
                        <Row>
                            <Col xs={4} className="key-container">
                                <p className="key">Name</p>
                                <p className="key">DOB</p>
                                <p className="key">Address</p>
                                <p className="key">Zip code</p>
                                <p className="key">Email</p>
                                <p className="key">Phone</p>
                            </Col>
                            <Col xs={8} className="value-container">
                                <p className="value">{userInfo.fullName}</p>
                                <p className="value">{userInfo.dob}</p>
                                <p className="value">{userInfo.addressLine2}</p>
                                <p className="value">{userInfo.zipCode}</p>
                                <p className="value">{userInfo.emailId}</p>
                                <p className="value">{userInfo.phoneNo}</p>
                            </Col>
                        </Row>
                        <br />
                        <div className="about-progress-rootContainer">
                            <div className="about-progress-container">
                                <h1 className="about-progress">{userInfo.projectCompleted > 9 ? userInfo.projectCompleted : '0' + userInfo.projectCompleted}</h1>
                                <p className="about-progress-title">Projects Completed</p>
                            </div>
                        </div>
                        <br />
                        <Button id="download-cv-but" aria-label="Download CV" onClick={downloadCv} >Download CV</Button>
                    </div>
                </Col>
            </Row>
        </Container>
    )
}